.about-highlight {
  color: #000;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.about-start {
  align-items: center;
  margin-bottom: 32px;
  display: flex;
}

.line-container {
  height: 2px;
  background: #000;
  border: 0;
  margin-left: 14px;
}

.about-div {
  background-image: url('../../images/mobile-flowers.jpg');
  background-position: left;
  background-repeat: no-repeat;
  background-size: auto;
  height: 178px;
  border-radius: 0px 639.5px 639.5px 0px;
  max-width: 288px;
  color: transparent;
  margin-bottom: 60px;
}

@media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .box {
    background-image: url('../../images/mobile-flowers@2x.jpg');
  }
}

.about-photo {
  max-width: 400px;
  background-color: white;
}

.about-paragraph {
  color: #000;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 16px;
}

/**tablet**/

@media screen and (min-width: 768px) {
  .about-div {
    background-image: url('../../images/tablet-flowers.jpg');
    background-size: auto;
    min-width: 344px;
    min-height: 217px;
  }

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .box {
      background-image: url('../../images/tablet-flowers@2x.jpg');
    }
  }
  .about-highlight {
    font-size: 44px;
  }

  .about-flex {
    display: flex;
    gap: 54px;
    align-items: center;
  }

  .about-paragraph {
    min-width: 322px;
    font-size: 20px;
  }
}

/** desktop **/

@media screen and (min-width: 1280px) {
  .about-div {
    background-image: url('../../images/desktop-flowers.jpg');
    min-width: 632px;
    min-height: 344px;
  }

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .box {
      background-image: url('../../images/desktop-flowers@2x.jpg');
    }
  }
  .about-flex {
    gap: 90px;
  }

  .about-paragraph {
    min-width: 510px;
    font-size: 22px;
  }
}
